html {
  height: 100%;
  width: 100%;
}
body {
  background-color: #F5F7FA;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main {
  flex: 1 0 auto;
  height: 100vh;
}
.footer {
  flex-shrink: 0;
}
.wrapper {
  width: 956px;
  margin: 0 auto;
}
.footer {
  border-top: 1px solid #DFE4EF;
  width: 100%;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__logo {
  display: flex;
  align-items: center;
}
.footer__logo--image {
  margin: 22px 27px 22px 0px;
  opacity: 0.3;
}
.footer__technic {
  display: flex;
  margin: 0;
  align-items: center;
}
.footer__technic--image {
  margin: 22px 8px 22px 0px;
}
.footer__figcaption {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
}
.footer__copyright {
  color: #333333;
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
}
.progress {
  margin-bottom: 52px;
  max-width: 764px;
  margin: 52px auto 52px auto;
}
.boxes {
  display: flex;
  height: 52px;
  justify-content: space-between;
  align-items: center;
}
.boxes .box {
  height: 4px;
  align-self: middle;
  position: relative;
  width: auto;
  flex: 1 0 auto;
  display: flex;
  align-items: center;
  background: #DFE4EF;
}
.boxes .box.past {
  background: #0066FF;
}
.boxes .box.past:nth-child(2) {
  background: linear-gradient(90deg, #0066FF 0%, #FF238D 100%);
}
.boxes .box.past .inner {
  width: 28px;
  height: 28px;
  background: #F5F7FA;
  border: 2px solid #0066FF;
  box-sizing: border-box;
  border-radius: 14px;
}
.boxes .box:last-of-type {
  flex: 0;
}
.boxes .box .inner {
  white-space: nowrap;
  min-width: 28px;
  max-width: 100%;
  position: relative;
  background: #fff;
  align-self: center;
  border-left: 1px solid #aaa;
  border-top: 1px solid #aaa;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  display: flex;
  min-height: 28px;
  max-height: 28px;
  background: #F5F7FA;
  border: 2px solid #DFE4EF;
  box-sizing: border-box;
  border-radius: 14px;
}
.boxes .box .legend {
  position: absolute;
  left: 50%;
  top: -35px;
  transform: translate(-50%, 0);
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
}
.boxes .box .legend-black {
  color: #333333;
}
.boxes .box .legend-blue {
  color: #0066FF;
}
.boxes .box.current .inner {
  min-height: 28px;
  max-height: 28px;
  background: #F5F7FA;
  border: 2px solid #FF238D;
  box-sizing: border-box;
  border-radius: 14px;
}
.box__number {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 21px;
  color: #333333;
}
.box__number--blue {
  color: #0066FF;
}
.table-main {
  width: 764px;
  min-height: 525px;
  background-color: #ffffff;
  box-shadow: 0px 1px 4px rgba(0, 15, 38, 0.1);
  border-radius: 6px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.table-main__title {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  color: #000000;
}
.table-wrap {
  width: 565px;
}
.timepicker {
  display: flex;
}
.timepicker__title {
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 108%;
  color: #000000;
  margin-right: 17px;
}
.timepicker__divider {
  margin: 0 12px;
}
.timepicker__date {
  border: none;
}
.timepicker__start-time,
.timepicker__end-time {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 108%;
  color: #000000;
  padding-bottom: 12px;
  border-bottom: 1px solid #BFC9E0;
}
.graph {
  max-width: 565px;
  display: flex;
  margin-top: 30px;
}
.graph__weekdays {
  margin-top: 30px;
  margin-right: 20px;
  width: 36px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}
.graph__weekdays--day {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 220%;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.graph__table {
  width: 100%;
  overflow-x: scroll;
}
.graph__wrapper {
  width: 510px;
  display: flex;
}
.graph__hours {
  list-style: none;
  flex-direction: column;
}
.graph__hours-numbers {
  flex-direction: row;
  display: -webkit-box;
}
.graph__hours li {
  width: 30px;
  height: 30px;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #DFE4EF;
  padding: 2px;
}
.graph__hours-row {
  flex-direction: row;
  display: -webkit-box;
}
.graph__buttons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 20px;
}
.graph__button {
  border: 1px solid #D3D8DF;
  box-sizing: border-box;
  border-radius: 50px;
  background-color: #ffffff;
  color: #000000;
  margin-left: 10px;
}
.graph__button:hover {
  background-color: #0066FF;
  color: #ffffff;
}
.graph__text {
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 108%;
  color: #000000;
  margin: 8px 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.graph__text--confirm:before {
  content: "";
  background-image: url("../../img/approve.svg");
  height: 14px;
  width: 15px;
  background-repeat: no-repeat;
  display: block;
  padding-right: 8px;
}
.graph__text--delete:before {
  content: "";
  background-image: url("../../img/delete.svg");
  height: 12px;
  width: 12px;
  background-repeat: no-repeat;
  display: block;
  padding-right: 8px;
}
.graph__hours .graph__hours--time {
  background-color: #ffffff;
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 14px;
  line-height: 220%;
  padding: 2px;
}
.graph__hours .graph__hours--time:nth-child(2n-1) {
  visibility: hidden;
}
.graph__hours .graph__hours--time:nth-child(2n) {
  border-left: 1px solid #DFE4EF;
}
.graph__hours .graph__hours--time:hover {
  background-color: #ffffff;
}
.graph__hours .graph__hours--time sup {
  font-size: 10px;
}
.graph__hours .active {
  background-color: #0066FF;
}
.navigation {
  display: flex;
  justify-content: center;
  margin: 25px auto;
}
.navigation__button {
  width: 100px;
  height: 38px;
  border: 1px solid #DFE4EF;
  box-sizing: border-box;
  border-radius: 50px;
  background-color: transparent;
  color: #000000;
  margin: 0px 5px;
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 108%;
}
.navigation__button:hover {
  background-color: #0066FF;
  color: #ffffff;
}
